.label__switch i
{
    position: relative;

    display: block;

    width: 40px;
    height: 20px;
    margin-right: 10px;

    cursor: pointer;

    border: 1px solid #ececec;
    background: #ececec;
}

.label__switch i::after
{
    display: block;

    width: 18px;
    height: 18px;

    content: ' ';
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    background: #408c09;
}

.label__radio i
{
    position: relative;

    display: block;

    width: 19px;
    height: 19px;
    margin-right: 10px;

    cursor: pointer;
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    border: 1px solid #ececec;
    border-radius: 50%;
    background: #fff;
}

.label__radio i::after
{
    display: block;
    display: none;

    width: 5px;
    height: 5px;
    margin: 6px;

    content: '';
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    border-radius: 50%;
    background: #000;
}

.label__checkbox i
{
    position: relative;

    display: inline-block;

    width: 24px;
    height: 24px;
    margin-right: 10px;

    cursor: pointer;
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    border: 1px solid #ececec;
    border-radius: 0;
    background: #fff;
}

.label__checkbox i::after
{
    position: relative;
    top: 3px;
    left: 8px;

    display: block;

    width: 6px;
    height: 10px;
    margin-top: -20px;

    content: '';
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    opacity: 0;
    border-right: 1px solid #fff;
    border-bottom: 1px solid #fff;

    -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=0)';
        filter: alpha(opacity=0);
}

.label__checkbox:hover i,
.label__radio:hover i
{
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    border-color: #025958;
}

.label__checkbox .input__checkbox:checked + i
{
    border-color: #408c09;
    background: #408c09;
}

.label__checkbox .input__checkbox:checked + i::after
{
    margin-top: 0;

    content: ' ';
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;
    -webkit-transform: rotateZ(40deg);
       -moz-transform: rotateZ(40deg);
        -ms-transform: rotateZ(40deg);
         -o-transform: rotateZ(40deg);
            transform: rotateZ(40deg);

    opacity: 1;

    -ms-filter: none;
        filter: none;
}

.label__radio .input__radio:checked + i
{
    border-color: #000;
    background: #fbae11;
}

.label__radio .input__radio:checked + i::after
{
    display: block;

    content: ' ';
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;
}

.label__switch .input__switch:checked + i::after
{
    margin-top: 0;
    margin-left: 20px;

    content: ' ';
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    opacity: 1;

    -ms-filter: none;
        filter: none;
}

